.title{
    margin-bottom: 5px;
    font-size:3em; 
}

.img{
  margin-left: 0px;
    margin-top: 0px;
    width: 400px;
    
}



@media screen and (max-width: 500px) {
    .title{
    font-size:1em; 
    }
  } 

/* PARA MODEFICAR A POSIÇÃO DA LOGO NA HEADER */
  
  @media screen and (min-width: 1000px) {
    .img {
        margin-left: 300px;
        
    }

  } 
  @media screen and (max-width: 1025px) {
    .img {
        margin-left: 130px;
        
    }

  } 
  @media screen and (max-width: 999px) {
    .img {
        margin-left: 110px;
        
    }

  } 
@media screen and (max-width: 745px) {
    .img {
      margin-left: 120px;
     width: 300px;
    }

  } 
  @media screen and (max-width: 540px) {
    .img {
      margin-left: 60px;
     width: 300px;
    }
    .title{
      font-size:1em; 
      }


  } 
@media screen and (max-width: 484px) {
    .img {
     width: 220px;
    }

} 
@media screen and (max-width: 484px) {
    .img {
    margin-left: 0px;
     width: 200px;
    }

  } 
